import React from 'react';
import styled from 'styled-components'

import barcode from '../assets/images/barcode.jpg'

const Ticket = ({ place, color, isWeekday, info }) => {

  const {name, position, title, serviceTime, reservationDate} = info

  const getTime = ({ title, serviceTime}) => {
    if (title === '수요예배') {
      return '저녁 7:30'
    } else if (title === '금요성령집회') {
      return '저녁 8:00'
    } else if (title === '주일예배') {
      switch (serviceTime) {
        case '1부':
          return '오전 7:10'
        case '2부':
          return '오전 8:00'
        case '3부':
          return '오전 9:30'
        case '4부':
          return '오전 11:00'
        case '6부':
          return '오후 4:00'
        default:
          return '오전 7:10'
      }
    }
  }


  return (
    <Container>
      <Header color={color}>
        <div style={{ fontSize: '4rem', fontWeight: 800, lineHeight: 1.2}}>티켓</div>
        <div style={{ fontSize: '4rem', fontWeight: 800, lineHeight: 0.8, textAlign: 'end'}}>TICKET</div>
      </Header>
      <Body>
        <div style={{fontSize: '1.5rem', fontWeight: 600, marginBottom: '1.75rem'}}>{name} {position}님 </div>
        <ResultContainer>
          {place === '실패' ? (
            <div style={{display: 'flex', flexDirection: 'column'}}>
              <span style={{fontSize: '1.75rem', fontWeight: 600, color:'red', marginBottom:'0.75rem', textDecoration: 'underline black'}}>신청이 마감되었습니다</span>
              <span style={{fontSize: '1.5rem', fontWeight: 600 }}>죄송합니다</span>
            </div>
          ) : (
            <span style={{borderBottom: '1px solid black', fontSize: '1.5rem'}}>
              <span style={{fontSize: '1.8rem', fontWeight: 600, color:'red'}}>{place}</span>에 신청되었습니다
            </span>
          )}
        </ResultContainer>
        <DetailContainer>
          <Detail>
            <div style={{fontSize:'1.25rem'}}>{title}</div>
            <div style={{fontSize:'0.75rem', color:'#A4A4A4'}}>예배</div>
          </Detail>
          {isWeekday ? (
            <div></div>
          ) : (
            <Detail>
              <div style={{fontSize:'1.25rem'}}>{serviceTime}</div>
              <div style={{fontSize:'0.75rem', color:'#A4A4A4'}}>부</div>
          </Detail>
          )}
        </DetailContainer>
        <DetailContainer>
          <Detail>
            <div style={{fontSize:'1.25rem'}}>{reservationDate.substring(0,4)+'.'+reservationDate.substring(4,6)+'.'+reservationDate.substring(6,8)+'.'}</div>
            <div style={{fontSize:'0.75rem', color:'#A4A4A4'}}>일시</div>
          </Detail>
          <Detail>
            <div style={{fontSize:'1.25rem'}}>{getTime({title, serviceTime})}</div>
            <div style={{fontSize:'0.75rem', color:'#A4A4A4'}}>시간</div>
          </Detail>
        </DetailContainer>
        <Barcode src={barcode} />
      </Body>
    </Container>
  )
}

const Container = styled.div`
  display: flex;
  flex-direction: column;
  width: 85%;
`;

const Header = styled.div`
  display: flex;
  flex-direction: column;
  background-color: ${(props) => props.color ?? "#F54E50"};
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
`;

const Body = styled.div`
  padding: 1.5rem 1rem;
  background-color: #e2e2e2;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
`;

const ResultContainer = styled.div`
  margin-bottom: 2rem;
`;

const DetailContainer = styled.div`
  display: flex;
  flex-direction: row;
  margin-bottom: 1.5rem;
`;

const Detail = styled.div`
  flex: 1;
`;
const Barcode = styled.img`
  margin-top: 1rem;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`;

export default Ticket;