import React, { useEffect, useContext, useState } from 'react';
import styled from 'styled-components'
import { useLocation } from "react-router-dom";

import NavBar from '../components/NavBar'
import Ticket from '../components/Ticket'

import { SettingContext } from '../context/SettingContext'
import { Firebase, db } from '../config/firebaseConfig'

const VIEW_HEIGHT = window.innerHeight

const ResultPage = () => {
  const [ result, setResult ] = useState(null)
  const [ setting, setSetting ] = useContext(SettingContext)
  const location = useLocation();
  const registerData = location.payload

  useEffect(() => {
    async function sendToCloud() {
      console.log('전송중...')
      if (setting.isSubmitting) {
        await sendingData()
        await setSetting({
          ...setting,
          isSubmitting: false
        })
      }
    }
    sendToCloud()
  }, [])

  const onClickClose = () => {
    window.open('','_self').close(); 
    // 카카오에서 페이지 닫기
    window.location.href="kakaotalk://inappbrowser/close"
  }

  const sendingData = async () => {
    const submitData = {
      name: registerData.name.trim(),
      position: registerData.position.trim(),
      division: registerData.division.trim(),
      submitTime: registerData.submitTime,
      index: 0
    }

    const statsRef = db.collection(registerData.title).doc(registerData.serviceTime).collection(registerData.reservationDate).doc('--stats--');
    const newuserRef = db.collection(registerData.title).doc(registerData.serviceTime).collection(registerData.reservationDate).doc();
    const userListRef = db.collection(registerData.title).doc(registerData.serviceTime).collection(registerData.reservationDate)
    const decrement = Firebase.firestore.FieldValue.increment(-1)

    let query = userListRef.where("name", "==", registerData.name).where("position", "==", registerData.position);
    query.get().then(querySnapshot => {
      if (querySnapshot.empty) {
        console.log('등록진행 계속')
        db.runTransaction((transaction) => {
          return transaction.get(statsRef).then((statsDoc) => {
            if (!statsDoc.exists) {
              console.log('Document does not exist')
            } 
            const remainingCount = statsDoc.data().ReservationCount
            transaction.update(statsRef, { ReservationCount: decrement }, { merge: true })
            transaction.set(newuserRef, {
              ...submitData,
              serverTime: Firebase.firestore.FieldValue.serverTimestamp(),
              index: Math.abs(remainingCount - 90) + 1,
            }) 
            }).then(() => {
              console.log("Transaction successfully committed!")
            }).catch((error) => {
              console.log("Transaction failed: ", error)
            })
        })
      } else {
        console.log('중복자로 인한 등록진행 중단')
      }
    })
  }

  const checkResult = () => {
    const userListRef = db.collection(registerData.title).doc(registerData.serviceTime).collection(registerData.reservationDate)
    let indexQuery = userListRef.where("index", "==", 90)
    let query = userListRef.where("name", "==", registerData.name).where("position", "==", registerData.position);
    query.get().then(querySnapshot => {
      if (querySnapshot.empty) {
        console.log('DB에 없음')
        setResult('fail')
      } else {
        querySnapshot.forEach((doc) => {
          if (doc.data().index < 71) {
            console.log('본당 당첨')
            setResult('firstSuccess')
          } else if (doc.data().index >= 71 && doc.data().index < 91) {
            if (doc.data().index === 90) {
              console.log('90번째 선착순 검사')
              indexQuery.get().then((querySnapshot) => {
                let tempArray = []
                querySnapshot.forEach((doc) => {
                  tempArray.push({
                   id: doc.id,
                   ...doc.data()
                  })
               })
               tempArray.sort((a, b) => {
                 return a.serverTime - b.serverTime
               })
               if (tempArray[0].name === registerData.name) {
                console.log('90번째 선착순 당첨')
                setResult('secondSuccess')
               } else {
                console.log('90번째 선착순 탈락')
                setResult('fail')
               }
              })
            } else {
              console.log('베다니홀 당첨')
              setResult('secondSuccess')
            }
          } else {
            console.log('90명 이후 신청자')
            setResult('fail')
          }
        })
      }
    })
  }

  return (
    <Container>
      <NavBar />
      <ResultContainer style={{height: VIEW_HEIGHT}}>
        {setting.isSubmitting ? (
          <>
            <div>...처리중...</div>
            <div>잠시만 기다려주세요</div>
          </>
        ) : (
          <>
          {result == null ? (
            <CheckContainer>
              <CheckButton onClick={() => checkResult()}>결과확인</CheckButton>
              <div style={{fontSize: '1.25rem', marginBottom: '0.25rem', textAlign:'center'}}>결과 확인 버튼을</div>
              <div style={{fontSize: '1.25rem', marginBottom: '0.25rem', textAlign:'center'}}>눌러주세요!</div>
            </CheckContainer>
          ) : (
            <>
            {result === 'firstSuccess' ? (
              <Ticket place='본당' color='#4FDAFF' isWeekday={setting.isWeekday} info={registerData}/>
            ) : (
              <>
              {result === 'secondSuccess' ? (
                <Ticket place='베다니홀' color='#57FFAD' isWeekday={setting.isWeekday} info={registerData}/>
              ) : (
                <Ticket place='실패' color='#F54F50' isWeekday={setting.isWeekday} info={registerData}/>
              )}
              </>
            )}
            <CloseButton onClick={onClickClose}>페이지 닫기</CloseButton>
            <div 
              style={{width: '90%', display:'flex', flexWrap:'wrap', textAlign:'center', justifyContent: 'center', marginBottom: '0.5rem'}}
            >
              뒤로 가기 버튼만 누르지 마시고 창을 닫아 주세요 *^^*
            </div>
            </>
          )}
          </>
        )}
      </ResultContainer>
    </Container>

  )
}

const Container = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const ResultContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const CheckContainer = styled.div`
  margin-bottom: 2rem;
`;

const CheckButton = styled.div`
  margin-bottom: 1rem; 
  background-color: #4D826C;
  padding: 0.75rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 1.5rem;
  font-weight: 600;
  border-radius: 16px;
`;

const CloseButton = styled.div`
  margin: 20px 0;
  border: 2px solid #228be6;
  color: #222;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
`;

export default ResultPage;